import { USER_REGISTER_FAIL, USER_REGISTER_REQUEST, USER_REGISTER_SUCCESS, USER_LOGIN_REQUEST, USER_LOGIN_SUCCESS, USER_LOGIN_FAIL, USER_LOGOUT } from "../actions/types/userTypes";



export const userRegisterReducer = ( state = {token: ""}, action) => {
     
    switch (action.type) {
        case USER_REGISTER_REQUEST:
            return {loading: true}
        case USER_REGISTER_SUCCESS:
            return {loading: false, userInfo: action.payload}
        case USER_REGISTER_FAIL:
            return {loading: false, errors: action.payload}
        case USER_LOGOUT: 
            return {}
        default:
            return state;
    }
}

export const userLoginReducer = ( state = {token: ""}, action) => {
     
    switch (action.type) {
        case USER_LOGIN_REQUEST:
            return {loading: true}
        case USER_LOGIN_SUCCESS:
            return {loading: false, userInfo: action.payload}
        case USER_LOGIN_FAIL:
            return {loading: false, errors: action.payload}
        case USER_LOGOUT: 
            return {}
        default:
            return state;
    }
}